export default {
  state: {
    Blogs: [],
  },
  getters: {
    getBlogs: (state) => state.Blogs,
  },
  mutations: {
    setBlogs(state, Blogs) {
      state.Blogs = Blogs;
    },
  },
  actions: {
    async fetchBlogs({ commit }) {
      try {
        const response = await fetch("/dental/api/blog.json"); // Adjust the path as needed
        const data = await response.json();

        // Commit the data to the store
        commit("setBlogs", data);
      } catch (error) {
        console.error("Error fetching Blogs:", error);
      }
    },
  },
};
